dditional properties for button version\n// iOS requires the button element instead of an anchor tag.\n// If you want the anchor version, it requires `href=\"#\"`.\n// See https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile\n\n// stylelint-disable-next-line selector-no-qualifying-type\nbutton.close {\n  padding: 0;\n  background-color: transparent;\n  border: 0;\n}\n\n// Future-proof disabling of clicks on `<a>` elements\n\n// stylelint-disable-next-line selector-no-qualifying-type\na.close.disabled {\n  pointer-events: none;\n}\n","// Inline code\ncode {\n  @include font-size($code-font-size);\n  color: $code-color;\n  word-wrap: break-word;\n\n  // Streamline the style when inside anchors to avoid broken underline and more\n  a > & {\n    color: inherit;\n  }\n}\n\n// User input typically entered via keyboard\nkbd {\n  padding: $kbd-padding-y $kbd-padding-x;\n  @include font-size($kbd-font-size);\n  color: $kbd-color;\n  background-color: $kbd-bg;\n  @include border-radius($border-radius-sm);\n  @include box-shadow($kbd-box-shadow);\n\n  kbd {\n    padding: 0;\n    @include font-size(100%);\n    font-weight: $nested-kbd-font-weight;\n    @include box-shadow(none);\n  }\n}\n\n// Blocks of code\npre {\n  display: block;\n  @include font-size($code-font-size);\n  color: $pre-color;\n\n  // Account for some code outputs that place code tags in pre tags\n  code {\n    @include font-size(inherit);\n    color: inherit;\n    word-break: normal;\n  }\n}\n\n// Enable scrollable blocks of code\n.pre-scrollable {\n  max-height: $pre-scrollable-max-height;\n  overflow-y: scroll;\n}\n","\n\n// Code\n\n$code-font-size:  87.5% !default;\n$code-color:      $pink !default;\n\n$kbd-padding-y:  .2rem !default;\n$kbd-padding-x:  .4rem !default;\n$kbd-font-size:  $code-font-size !default;\n$kbd-color:      $white !default;\n$kbd-bg:         $gray-900 !default;\n\n$pre-color:                  $gray-900 !default;\n$pre-scrollable-max-height:  340px !default;\n","// Embedded icons from Open Iconic.\n// Released under MIT and copyright 2014 Waybury.\n// https://useiconic.com/open\n\n\n// Checkboxes and radios\n//\n// Base class takes care of all the key behavioral aspects.\n\n.custom-control {\n  position: relative;\n  display: block;\n  min-height: $font-size-base * $line-height-base;\n  @include ltr {\n    padding-left: $custom-control-gutter + $custom-control-indicator-size;\n  }\n  @include rtl {\n    padding-right: $custom-control-gutter + $custom-control-indicator-size;\n  }\n}\n\n.custom-control-inline {\n  display: inline-flex;\n  margin-right: $custom-control-spacer-x;\n}\n\n.custom-control-input {\n  position: absolute;\n  @include ltr {\n    left: 0;\n  }\n  @include rtl {\n    right: 0;\n  }\n  z-index: -1; // Put the input behind the label so it doesn't overlay text\n  width: $custom-control-indicator-size;\n  height: ($font-size-base * $line-height-base + $custom-control-indicator-size) / 2;\n  opacity: 0;\n\n  &:checked ~ .custom-control-label::before {\n    @include box-shadow($custom-control-indicator-checked-box-shadow);\n    @include themes($custom-forms-theme-map, $create: parent) {\n      color: themes-get-value(\"custom-control-indicator-checked-color\");\n      border-color: themes-get-value(\"custom-control-indicator-checked-border-color\");\n      @include gradient-bg(themes-get-value(\"custom-control-indicator-checked-bg\"));\n    }\n  }\n\n  &:focus ~ .custom-control-label::before {\n    // the mixin is not used here to make sure there is feedback\n    @if $enable-shadows {\n      box-shadow: $input-box-shadow, $input-focus-box-shadow;\n    } @else {\n      box-shadow: $custom-control-indicator-focus-box-shadow;\n    }\n  }\n\n  &:focus:not(:checked) ~ .custom-control-label::before {\n    @include themes($custom-forms-theme-map, $create: parent) {\n      border-color: themes-get-value(\"custom-control-indicator-focus-border-color\");\n    }\n  }\n\n  &:not(:disabled):active ~ .custom-control-label::before {\n    @include themes($custom-forms-theme-map, $create: parent) {\n      color: themes-get-value(\"custom-control-indicator-active-color\");\n      background-color: themes-get-value(\"custom-control-indicator-active-bg\");\n      border-color: themes-get-value(\"custom-control-indicator-active-border-color\");\n    }\n    @include box-shadow($custom-control-indicator-active-box-shadow);\n  }\n\n  // Use [disabled] and :disabled to work around https://github.com/twbs/bootstrap/issues/28247\n  &[disabled],\n  &:disabled {\n    ~ .custom-control-label {\n      @include themes($custom-forms-theme-map, $create: parent) {\n        color: themes-get-value(\"custom-control-label-disabled-color\");\n      }\n\n      &::before {\n        @include themes($custom-forms-theme-map, $create: parent) {\n          background-color: themes-get-value(\"custom-control-indicator-disabled-bg\");\n        }\n      }\n    }\n  }\n}\n\n// Custom control indicators\n//\n// Build the custom controls out of pseudo-elements.\n\n.custom-control-label {\n  position: relative;\n  margin-bottom: 0;\n  vertical-align: top;\n  cursor: $custom-control-cursor;\n  @include themes($custom-forms-theme-map, $create: parent) {\n    background-color: themes-get-value(\"custom-control-label-color\");\n  }\n\n  // Background-color and (when enabled) gradient\n  &::before {\n    position: absolute;\n    top: ($font-size-base * $line-height-base - $custom-control-indicator-size) / 2;\n    @include ltr {\n      left: -($custom-control-gutter + $custom-control-indicator-size);\n    }\n    @include rtl {\n      right: -($custom-control-gutter + $custom-control-indicator-size);\n    }\n    display: block;\n    width: $custom-control-indicator-size;\n    height: $custom-control-indicator-size;\n    pointer-events: none;\n    content: \"\";\n    border: solid $custom-control-indicator-border-width;\n    @include box-shadow($custom-control-indicator-box-shadow);\n    @include themes($custom-forms-theme-map, $create: parent) {\n      background-color: themes-get-value(\"custom-control-indicator-bg\");\n      border-color: themes-get-value(\"custom-control-indicator-border-color\");\n    }\n  }\n\n  // Foreground (icon)\n  &::after {\n    position: absolute;\n    top: ($font-size-base * $line-height-base - $custom-control-indicator-size) / 2;\n    @include ltr {\n      left: -($custom-control-gutter + $custom-control-indicator-size);\n    }\n    @include rtl {\n      right: -($custom-control-gutter + $custom-control-indicator-size);\n    }\n    display: block;\n    width: $custom-control-indicator-size;\n    height: $custom-control-indicator-size;\n    content: \"\";\n    background: no-repeat 50% / #{$custom-control-indicator-bg-size};\n  }\n}\n\n\n// Checkboxes\n//\n// Tweak just a few things for checkboxes.\n\n.custom-checkbox {\n  .custom-control-label::before {\n    @include border-radius($custom-checkbox-indicator-border-radius);\n  }\n\n  .custom-control-input:checked ~ .custom-control-label {\n    &::after {\n      @include themes($custom-forms-theme-map, $create: parent) {\n        background-image: escape-svg(themes-get-value(\"custom-checkbox-indicator-icon-checked\"));\n      }\n    }\n  }\n\n  .custom-control-input:indeterminate ~ .custom-control-label {\n    &::before {\n      @include themes($custom-forms-theme-map, $create: parent) {\n        border-color: themes-get-value(\"custom-checkbox-indicator-indeterminate-border-color\");\n      }\n      @include gradient-bg($custom-checkbox-indicator-indeterminate-bg);\n      @include box-shadow($custom-checkbox-indicator-indeterminate-box-shadow);\n    }\n    &::after {\n      background-image: escape-svg($custom-checkbox-indicator-icon-indeterminate);\n    }\n  }\n\n  .custom-control-input:disabled {\n    &:checked ~ .custom-control-label::before {\n      @include themes($custom-forms-theme-map, $create: parent) {\n        background-color: themes-get-value(\"custom-control-indicator-checked-disabled-bg\");\n      }\