v-link,\n    .c-subheader-nav-link.c-show,\n    .c-subheader-nav-link.c-active {\n      color: themes-get-value(\"subheader-active-color\");\n    }\n  }\n\n  .c-subheader-text {\n    color: themes-get-value(\"subheader-color\");\n    a {\n      color: themes-get-value(\"subheader-active-color\");\n\n      @include hover-focus {\n        color: themes-get-value(\"subheader-active-color\");\n      }\n    }\n  }\n}\n","// Subheader\n\n$subheader-height:       48px !default;\n\n$subheader-padding-y:  $spacer / 2 !default;\n$subheader-padding-x:  $spacer !default;\n\n$subheader-nav-link-padding-x:  .5rem !default;\n\n\n// Default theme\n\n$subheader-dark-bg:                     $gray-base !default;\n$subheader-dark-color:                  rgba($white, .75) !default;\n$subheader-dark-border:                 ( bottom: 1px solid $gray-800 ) !default;\n$subheader-dark-hover-color:            rgba($white, .9) !default;\n$subheader-dark-active-color:           $white !default;\n$subheader-dark-disabled-color:         rgba($white, .25) !default;\n\n$subheader-light-bg:                     $white !default;\n$subheader-light-color:                  rgba($black, .5) !default;\n$subheader-light-border:                 ( bottom: 1px solid $border-color ) !default;\n$subheader-light-hover-color:            rgba($black, .7) !default;\n$subheader-light-active-color:           rgba($black, .9) !default;\n$subheader-light-disabled-color:         rgba($black, .3) !default;\n\n\n$subheader-variants-map: () !default;\n// stylelint-disable-next-line scss/dollar-variable-default\n$subheader-variants-map: map-merge(\n  (\n    dark: (\n      \"subheader-bg\":                      $subheader-dark-bg,\n      \"subheader-color\":                   $subheader-dark-color,\n      \"subheader-border\":                  $subheader-dark-border,\n      \"subheader-hover-color\":             $subheader-dark-hover-color,\n      \"subheader-active-color\":            $subheader-dark-active-color,\n      \"subheader-disabled-color\":          $subheader-dark-disabled-color,\n    ),\n    light: (\n      \"subheader-bg\":                      $subheader-light-bg,\n      \"subheader-color\":                   $subheader-light-color,\n      \"subheader-border\":                  $subheader-light-border,\n      \"subheader-hover-color\":             $subheader-light-hover-color,\n      \"subheader-active-color\":            $subheader-light-active-color,\n      \"subheader-disabled-color\":          $subheader-light-disabled-color,\n    )\n  ),\n  $subheader-variants-map\n);\n\n$subheader-default-variant: \"light\" !default;\n$subheader-variants: (\n  default:  $subheader-default-variant,\n  map:      $subheader-variants-map\n) !default;\n\n$subheader-theme-map: () !default;\n// stylelint-disable-next-line scss/dollar-variable-default\n$subheader-theme-map: map-merge(\n  (\n    default:  $subheader-variants\n  ),\n  $subheader-theme-map\n);\n","//\n// Base styles\n//\n\n.c-switch {\n  display: inline-block;\n  width: $switch-width;\n  height: $switch-height;\n}\n\n.c-switch-input {\n  position: absolute;\n  z-index: -1;\n  opacity: 0;\n}\n\n.c-switch-slider {\n  position: relative;\n  display: block;\n  height: inherit;\n  cursor: pointer;\n  background-color: $white;\n  border: $border-width solid $border-color;\n  transition: .15s ease-out;\n  @include border-radius($border-radius);\n\n  &::before {\n    position: absolute;\n    top: $switch-handle-margin;\n    left: $switch-handle-margin;\n    box-sizing: border-box;\n    width: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\n    height: $switch-height - ($switch-handle-margin * 2) - ($border-width * 2);\n    content: \"\";\n    background-color: $white;\n    border: 1px solid $border-color;\n    transition: .15s ease-out;\n    @include border-radius($border-radius / 2);\n  }\n}\n\n.c-switch-input:checked ~ .c-switch-slider {\n  &::before {\n    transform: translateX($switch-width - $switch-height);\n  }\n}\n\n.c-switch-input:focus ~ .c-switch-slider {\n  @include themes($form-theme-map) {\n    color: themes-get-value(\"input-focus-color\");\n    background-color: themes-get-value(\"input-focus-bg\");\n    border-color: themes-get-value(\"input-focus-border-color\");\n  }\n  outline: 0;\n  // Avoid using mixin so we can pass custom focus shadow properly\n  @if $enable-shadows {\n    box-shadow: $input-box-shadow, $input-focus-box-shadow;\n  } @else {\n    box-shadow: $input-focus-box-shadow;\n  }\n}\n\n.c-switch-input:disabled ~ .c-switch-slider {\n  cursor: not-allowed;\n  opacity: .5;\n}\n\n//\n// Switch Sizes\n//\n\n.c-switch-lg {\n  @include switch-size($switch-lg-width, $switch-lg-height, $switch-lg-font-size, $switch-handle-margin);\n}\n\n.c-switch-sm {\n  @include switch-size($switch-sm-width, $switch-sm-height, $switch-sm-font-size, $switch-handle-margin);\n}\n\n//\n// Switch with label\n//\n.c-switch-label {\n  width: $switch-label-width;\n\n  .c-switch-slider {\n    &::before {\n      z-index: 2;\n    }\n    &::after {\n      position: absolute;\n      top: 50%;\n      z-index: 1;\n      width: 50%;\n      margin-top: -.5em;\n      font-size: $switch-font-size;\n      font-weight: 600;\n      line-height: 1;\n      color: $gray-300;\n      text-align: center;\n      text-transform: uppercase;\n      content: attr(data-unchecked);\n      transition: inherit;\n\n      @include ltr {\n        right: 1px;\n      }\n    }\n  }\n\n  .c-switch-input:checked ~ .c-switch-slider {\n    &::before {\n      transform: translateX($switch-label-width - $switch-height);\n    }\n    &::after {\n      left: 1px;\n      color: $white;\n      content: attr(data-checked);\n    }\n  }\n\n  // Sizes\n  &.c-switch-lg {\n    @include switch-size($switch-label-lg-width, $switch-lg-height, $switch-lg-font-size, $switch-handle-margin);\n  }\n\n  &.c-switch-sm {\n    @include switch-size($switch-label-sm-width, $switch-sm-height, $switch-sm-font-size, $switch-handle-margin);\n  }\n}\n\n//\n// Switch 3d\n//\n\n.c-switch[class*=\"-3d\"] {\n  .c-switch-slider {\n    background-color: $gray-100;\n    border-radius: 50em;\n\n    &::before {\n      top: -1px;\n      left: -1px;\n      width: $switch-height;\n      height: $switch-height;\n      border: 0;\n      border-radius: 50em;\n      box-shadow: 0 2px 5px rgba($black, .3);\n    }\n  }\n\n  // Sizes\n  &.c-switch-lg {\n    @include switch-3d-size($switch-lg-width, $switch-lg-height);\n  }\n\n  &.c-switch-sm {\n    @include switch-3d-size($switch-sm-width, $switch-sm-height);\n  }\n}\n\n//\n// Switch Variants\n//\n\n@include theme-variant() {\n  //normal style\n  .c-switch-#{$color} {\n    @include switch-variant($value);\n  }\n  // 3s style\n  .c-switch-3d-#{$color} {\n    @include switch-3d-variant($value);\n  }\n  // outline style\n  .c-switch-outline-#{$color} {\n    @include switch-outline-variant($value);\n  }\n  // oposite\n  .c-switch-opposite-#{$color} {\n    @include switch-opposite-variant($value);\n  }\n}\n\n//\n// Pill Style\n//\n\n.c-switch-pill {\n  .c-switch-slider {\n    border-radius: 50em;\n\n    &::before {\n      border-radius: 50em;\n    }\n  }\n}\n\n.c-switch-square {\n  .c-switch-slider {\n    border-radius: 0;\n\n    &::before {\n      border-radius: 0;\n    }\n  }\n}\n","// Switches\n\n$switch-width:      40px !default;\n$switch-height:     26px !default;\n$switch-font-size:  10px !default;\n\n$switch-lg-width:      48px !default;\n$switch-lg-height:     30px !default;\n$switch-lg-font-size:  12px !default;\n\n$switch-sm-width:      32px !default;\n$switch-sm-height:     22px !default;\n$switch-sm-font-size:  8px !default;\n\n$switch-label-width:     48px !default;\n$switch-label-lg-width:  56px !default;\n$switch-label-sm-width:  40px !default;\n\n$switch-handle-margin:  2px !default;\n","@mixin switch-size($width, $height, $font-size, $handle-margin) {\n  width: $width;\n  height: $height;\n\n  .c-switch-slider {\n    font-size: $font-size;\n\n    &::before {\n      width: $height - ($handle-margin * 2) - ($border-width * 2);\n      height: $height - ($handle-margin * 2) - ($border-width * 2);\n    }\n    &::after {\n      font-size: $font-size;\n    }\n  }\n\n  .c-switch-input:checked ~ .c-switch-slider {\n    &::before {\n      transform: translateX($width - $height);\n    }\n  }\n}\n\n@mixin switch-3d-size($width, $height) {\n  width: $width;\n  height: $height;\n\n  .c-switch-slider::before {\n    width: $height;\n    height: $height;\n  }\n\n  .c-switch-input:checked ~ .c-switch-slider {\n    &::before {\n      transform: translateX($width - $height);\n    }\n  }\n}\n\n@mixin switch-3d-variant($color) {\n  .c-switch-input:checked + .c-switch-slider {\n    background-color: $color;\n  }\n}\n\n@mixin switch-variant($color) {\n  .c-switch-input:checked + .c-switch-slider {\n    background-color: $color;\n    border-color: darken($color, 10%);\n\n    &::before {\n      border-color: darken($color, 10%);\n    }\n  }\n}\n\n@mixin switch-outline-variant($color) {\n  .c-switch-input:checked + .c-switch-slider {\n    background-color: $white;\n    border-color: $color;\n\n    &::before {\n      border-color: $color;\n    }\n    &::after {\n      color: $color;\n    }\n  }\n}\n\n@mixin switch-opposite-variant($color) {\n  .c-switch-input:checked + .c-switch-slider {\n    background-color: $white;\n    border-color: $color;\n\n    &::before {\n      background-color: $color;\n      border-color: $color;\n    }\n    &::after {\n      color: $color;\n    }\n  }\n}\n","//\n// Basic Bootstrap table\n//\n\n.table {\n  width: 100%;\n  margin-bottom: $spacer;\n\n  @include themes($table-theme-map, $create: parent) {\n    color: themes-get-value(\"table-color\");\n    background-color: themes-get-value(\"table-bg\");\n  }\n\n  th,\n  td {\n    padding: $table-cell-padding;\n    vertical-align: top;\n    border-top: $table-border-width solid;\n\n    @include themes($table-theme-map, $create: parent) {\n      border-top-color: themes-get-value(\"table-border-color\");\n    }\n  }\n\n  thead th {\n    vertical-align: bottom;\n    border-bottom: (2 * $table-border-width) solid;\n\n    @include themes($table-theme-map, $create: parent) {\n      border-bottom-color: themes-get-value(\"table-border-color\");\n    }\n  }\n\n  tbody + tbody {\n    border-top: (2 * $table-border-width) solid;\n\n    @include themes($table-theme-map, $create: parent) {\n      border-top-color: themes-get-value(\"table-border-color\");\n    }\n  }\n}\n\n\n//\n// Condensed table w/ half padding\n//\n\n.table-sm {\n  th,\n  td {\n    padding: $table-cell-padding-sm;\n  }\n}\n\n\n// Border versions\n//\n// Add or remove borders all around the table and between all the columns.\n\n.table-bordered {\n  border: $table-border-width solid;\n\n  @include themes($table-theme-map, $create: parent) {\n    border-color: themes-get-value(\"table-border-color\");\n  }\n\n  th,\n  td {\n    border: $table-border-width solid;\n\n    @include themes($table-theme-map, $create: parent) {\n      border-color: themes-get-value(\"table-border-color\");\n    }\n  }\n\n  thead {\n    th,\n    td {\n      border-bottom-width: 2 * $table-border-width;\n    }\n  }\n}\n\n.table-borderless {\n  th,\n  td,\n  thead th,\n  tbody + tbody {\n    border: 0;\n  }\n}\n\n// Zebra-striping\n//\n// Default zebra-stripe styles (alternating gray and transparent backgrounds)\n\n.table-striped {\n  tbody tr:nth-of-type(#{$table-striped-order}) {\n\n    @include themes($table-theme-map, $create: parent) {\n      background-color: themes-get-value(\"table-accent-bg\");\n    }\n  }\n}\n\n\n// Hover effect\n//\n// Placed here since it has to come after the potential zebra striping\n\n.table-hover {\n  tbody tr {\n    @include hover() {\n      @include themes($table-theme-map, $create: parent) {\n        color: themes-get-value(\"table-hover-color\");\n        background-color: themes-get-value(\"table-hover-bg\");\n      }\n    }\n  }\n}\n\n\n// Table backgrounds\n//\n// Exact selectors below required to override `.table-striped` and prevent\n// inheritance to nested tables.\n\n@include theme-variant() {\n  @include table-row-variant($color,  theme-color-level($value, $table-bg-level),  theme-color-level($value, $table-border-level));\n}\n\n@include themes($table-theme-map, $create: parent) {\n  @include table-row-variant(active, themes-get-value(\"table-active-bg\"));\n  @include table-row-variant(selected, themes-get-value(\"table-active-bg\"), themes-get-value(\"table-active-bg\"));\n}\n\n// stylelint-disable-next-line no-duplicate-selectors\n.table {\n  tbody tr {\n    &:focus {\n      outline: 0;\n      @include themes($table-theme-map, $create: parent) {\n        color: themes-get-value(\"table-hover-color\");\n        background-color: themes-get-value(\"table-hover-bg\");\n      }\n    }\n  }\n}\n\n\n// Dark styles\n//\n// Same table markup, but inverted color scheme: dark background and light text.\n\n// stylelint-disable-next-line no-duplicate-selectors\n.table {\n  .thead-dark {\n    th {\n      @include themes($table-theme-map) {\n        color: themes-get-value(\"table-dark-color\");\n        background-color: themes-get-value(\"table-dark-bg\");\n        border-color: themes-get-value(\"table-dark-border-color\");\n      }\n    }\n  }\n\n  .thead-light {\n    th {\n      @include themes($table-theme-map) {\n        color: themes-get-value(\"table-head-color\");\n        background-color: themes-get-value(\"table-head-bg\");\n        border-color: themes-get-value(\"table-border-color\");\n      }\n    }\n  }\n}\n\n.table-dark {\n  @include themes($table-theme-map) {\n    color: themes-get-value(\"table-dark-color\");\n    background-color: themes-get-value(\"table-dark-bg\");\n  }\n\n  th,\n  td,\n  thead th {\n    @include themes($table-theme-map) {\n      border-color: themes-get-value(\"table-dark-border-color\");\n    }\n  }\n\n  &.table-bordered {\n    border: 0;\n  }\n\n  &.table-striped {\n    tbody tr:nth-of-type(#{$table-striped-order}) {\n      @include themes($table-theme-map) {\n        background-color: themes-get-value(\"table-dark-accent-bg\");\n      }\n    }\n  }\n\n  &.table-hover {\n    tbody tr {\n      @include hover() {\n        @include themes($table-theme-map) {\n          color: themes-get-value(\"table-dark-hover-color\");\n          background-color: themes-get-value(\"table-dark-hover-bg\");\n        }\n      }\n    }\n  }\n}\n\n\n// Responsive tables\n//\n// Generate series of `.table-responsive-*` classes for configuring the screen\n// size of where your table will overflow.\n\n.table-responsive {\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    $next: breakpoint-next($breakpoint, $grid-breakpoints);\n    $infix: breakpoint-infix($next, $grid-breakpoints);\n\n    &#{$infix} {\n      @include media-breakpoint-down($breakpoint) {\n        display: block;\n        width: 100%;\n        overflow-x: auto;\n        -webkit-overflow-scrolling: touch;\n\n        // Prevent double border on horizontal scroll due to use of `display: block;`\n        > .table-bordered {\n          border: 0;\n        }\n      }\n    }\n  }\n}\n\n.table-outline {\n  border: 1px solid;\n\n  @include themes($table-theme-map, $create: parent) {\n    border-color: themes-get-value(\"table-border-color\");\n  }\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-align-middle {\n\n  td {\n    vertical-align: middle;\n  }\n}\n\n.table-clear {\n  td {\n    border: 0;\n  }\n}\n","// Tables\n\n@mixin table-row-variant($state, $background, $border: null) {\n  // Exact selectors below required to override `.table-striped` and prevent\n  // inheritance to nested tables.\n  .table-#{$state} {\n    &,\n    > th,\n    > td {\n      @if map-has-key($yiq-theme-map, $current-theme) {\n        color: color-yiq($background, map-get(map-get($yiq-theme-map, $current-theme), \"yiq-text-dark\"), map-get(map-get($yiq-theme-map, $current-theme), \"yiq-text-light\"));\n      }\n      background-color: $background;\n    }\n\n    @if $border != null {\n      th,\n      td,\n      thead th,\n      tbody + tbody {\n        border-color: $border;\n      }\n    }\n  }\n\n  // Hover states for `.table-hover`\n  // Note: this is not available for cells or rows within `thead` or `tfoot`.\n  .table-hover {\n    $hover-background: darken($background, 5%);\n\n    .table-#{$state} {\n      @include hover() {\n        background-color: $hover-background;\n\n        > td,\n        > th {\n          background-color: $hover-background;\n        }\n      }\n    }\n  }\n}\n",".toast {\n  width: $toast-max-width;\n  max-width: $toast-max-width;\n  overflow: hidden; // cheap rounded corners on nested items\n  @include font-size($toast-font-size);\n  background-clip: padding-box;\n  border: $toast-border-width solid;\n  box-shadow: $toast-box-shadow;\n  backdrop-filter: blur(10px);\n  opacity: 0;\n  @include border-radius($toast-border-radius);\n  @include themes($toast-theme-map, $create: parent) {\n    color: themes-get-value(\"toast-color\");\n    background-color: themes-get-value(\"toast-background-color\");\n    border-color: themes-get-value(\"toast-border-color\");\n  }\n\n  &:not(:last-child) {\n    margin-bottom: $toast-padding-x;\n  }\n\n  &.showing {\n    opacity: 1;\n  }\n\n  &.show {\n    display: block;\n    opacity: 1;\n  }\n\n  &.hide {\n    display: none;\n  }\n}\n\n.toast-full {\n  width: 100%;\n  max-width: 100%;\n}\n\n.toast-header {\n  display: flex;\n  align-items: center;\n  padding: $toast-padding-y $toast-padding-x;\n  background-clip: padding-box;\n  border-bottom: $toast-border-width solid;\n  @include themes($toast-theme-map, $create: parent) {\n    color: themes-get-value(\"toast-header-color\");\n    background-color: themes-get-value(\"toast-header-background-color\");\n    border-color: themes-get-value(\"toast-header-border-color\");\n  }\n}\n\n.toast-body {\n  padding: $toast-padding-x; // apply to both vertical and horizontal\n}\n\n.toaster 