tion-name: fadeIn;\n  animation-duration: 1s;\n}\n\n//\n// Components\n//\n\n.c-wrapper {\n  transition: margin $layout-transition-speed;\n}\n\n.c-sidebar {\n  transition: box-shadow $layout-transition-speed $layout-transition-speed / 2, transform $layout-transition-speed, margin-left $layout-transition-speed, margin-right $layout-transition-speed, width $layout-transition-speed, z-index 0s ease $layout-transition-speed;\n  &.c-sidebar-unfoldable {\n    transition: transform $layout-transition-speed, margin-left $layout-transition-speed, margin-right $layout-transition-speed, width $layout-transition-speed, z-index 0s ease 0s;\n  }\n}\n\n.c-no-layout-transition {\n  .c-wrapper,\n  .c-sidebar {\n    transition: none;\n\n    .c-sidebar-header,\n    .c-sidebar-nav-title,\n    .c-sidebar-nav-divider,\n    .c-sidebar-nav-link,\n    .c-sidebar-nav-icon,\n    .c-sidebar-nav-dropdown,\n    .c-sidebar-nav-dropdown-toggle,\n    .c-sidebar-nav-dropdown-toggle::after,\n    .c-sidebar-nav-dropdown-items,\n    .c-sidebar-nav-label,\n    .c-sidebar-footer,\n    .c-sidebar-minimizer  {\n      transition: none;\n    }\n  }\n}\n\n.c-no-transition {\n  transition: none;\n}\n","// stylelint-disable declaration-no-important, selector-list-comma-newline-after\n\n//\n// Headings\n//\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6 {\n  margin-bottom: $headings-margin-bottom;\n  font-family: $headings-font-family;\n  font-weight: $headings-font-weight;\n  line-height: $headings-line-height;\n  color: $headings-color;\n}\n\nh1, .h1 { @include font-size($h1-font-size); }\nh2, .h2 { @include font-size($h2-font-size); }\nh3, .h3 { @include font-size($h3-font-size); }\nh4, .h4 { @include font-size($h4-font-size); }\nh5, .h5 { @include font-size($h5-font-size); }\nh6, .h6 { @include font-size($h6-font-size); }\n\n.lead {\n  @include font-size($lead-font-size);\n  font-weight: $lead-font-weight;\n}\n\n// Type display classes\n.display-1 {\n  @include font-size($display1-size);\n  font-weight: $display1-weight;\n  line-height: $display-line-height;\n}\n.display-2 {\n  @include font-size($display2-size);\n  font-weight: $display2-weight;\n  line-height: $display-line-height;\n}\n.display-3 {\n  @include font-size($display3-size);\n  font-weight: $display3-weight;\n  line-height: $display-line-height;\n}\n.display-4 {\n  @include font-size($display4-size);\n  font-weight: $display4-weight;\n  line-height: $display-line-height;\n}\n\n\n//\n// Horizontal rules\n//\n\nhr {\n  margin-top: $hr-margin-y;\n  margin-bottom: $hr-margin-y;\n  border: 0;\n  border-top: $hr-border-width solid $hr-border-color;\n}\n\n//\n// Vertical rules\n//\n\n.c-vr {\n  width: $vr-width;\n  background-color: $vr-color-bg;\n}\n\n//\n// Emphasis\n//\n\nsmall,\n.small {\n  @include font-size($small-font-size);\n  font-weight: $font-weight-normal;\n}\n\nmark,\n.mark {\n  padding: $mark-padding;\n  background-color: $mark-bg;\n}\n\n\n//\n// Lists\n//\n\n.list-unstyled {\n  @include list-unstyled();\n}\n\n// Inline turns list items into inline-block\n.list-inline {\n  @include list-unstyled();\n}\n.list-inline-item {\n  display: inline-block;\n\n  &:not(:last-child) {\n    margin-right: $list-inline-padding;\n  }\n}\n\n\n//\n// Misc\n//\n\n// Builds on `abbr`\n.initialism {\n  @include font-size(90%);\n  text-transform: uppercase;\n}\n\n// Blockquotes\n.blockquote {\n  margin-bottom: $spacer;\n  @include font-size($blockquote-font-size);\n}\n\n.blockquote-footer {\n  display: block;\n  @include font-size($blockquote-small-font-size);\n  color: $blockquote-small-color;\n\n  &::before {\n    content: \"\\2014\\00A0\"; // em dash, nbsp\n  }\n}\n","//\n// CoreUI default layout\n//\n\n.c-app {\n  display: flex;\n  flex-direction: row;\n  min-height: 100vh;\n}\n\n// IE10&11 Flexbox fix\n@media all and (-ms-high-contrast: none) {\n  html {\n    display: flex;\n    flex-direction: column;\n  }\n}\n\n.c-wrapper {\n  display: flex;\n  flex: 1;\n  flex-direction: column;\n  min-width: 0;\n  min-