@include deprecate(\"The `bg-variant` mixin\", \"v4.4.0\", \"v5\", $ignore-warning);\n}\n\n@mixin bg-gradient-variant($parent, $color, $ignore-warning: false) {\n  #{$parent} {\n    background: $color linear-gradient(180deg, mix($body-bg, $color, 15%), $color) repeat-x !important;\n  }\n  @include deprecate(\"The `bg-gradient-variant` mixin\", \"v4.5.0\", \"v5\", $ignore-warning);\n}\n\n@mixin coreui-bg-gradient-variant($parent, $colors) {\n  #{$parent} {\n    background: map-get($colors, \"stop\") !important;\n    background: linear-gradient(45deg, map-get($colors, \"start\") 0%, map-get($colors, \"stop\") 100%) !important;\n    border-color: map-get($colors, \"stop\") !important;\n  }\n}\n","// stylelint-disable declaration-no-important\n@include theme-variant() {\n  @include bg-variant(\".bg-#{$color}\", $value, trur);\n}\n\n// @if $enable-gradients {\n//   @include theme-variant() {\n//     @include bg-gradient-variant(\".bg-gradient-#{$color}\", $value, true);\n//   }\n// }\n\n@include theme-gradients() {\n  @include coreui-bg-gradient-variant(\".bg-gradient-#{$gradient}\", $value);\n}\n\n.bg-white {\n  background-color: $white !important;\n}\n\n.bg-transparent {\n  background-color: transparent !important;\n}\n\n[class^=\"bg-\"] {\n  color: $white;\n}\n\n@each $color, $value in $brands-colors {\n  @include bg-variant(\".bg-#{$color}\", $value);\n}\n\n@each $color, $value in $grays {\n  @include bg-variant(\".bg-gray-#{$color}\", $value);\n}\n\n.bg-box {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: ($spacer * 2.5);\n  height: ($spacer * 2.5);\n}\n","// stylelint-disable property-blacklist, declaration-no-important\n\n//\n// Border\n//\n\n@include themes($border-theme-map) {\n  .border         { border: $border-width solid themes-get-value(\"border-color\") !important; }\n  .border-top     { border-top: $border-width solid themes-get-value(\"border-color\") !important; }\n  .border-right   { border-right: $border-width solid th